import { createRouter, createWebHistory } from "vue-router";
import Register from '@/pages/Register.vue'
import Homepage from '@/pages/HomePage.vue'
import notFound from '@/pages/404NotFound.vue'
import cartPage from '@/pages/CartPage'
import ALk from '@/pages/LK'
import Autoelektrika from '@/pages/catalogPages/Autoelektrika'




export default createRouter({
    history: createWebHistory(),
    routes:[
        {path:'/homepage', 
            component: Homepage, 
            alias:'/',  
            name:'Главная', 
        },

        {
            path:'/homepage/register', 
            component: Register, 
            name:'/Регистрация',
        },
        {path:'/:pathMatch(.*)*', component:notFound, name:'404'},
        {path:'/homepage/lk/cart', component:cartPage, name:'/Корзина'},
        {path:'/homepage/lk', component:ALk, name:'/Личный кабинет'},
        {path:'/autoelektrika', component:Autoelektrika, name:'/АвтоЭлектрика', title:'Автоэлектрика'},
        {path:'/aimpuls', component:Autoelektrika, name:'/Aimpuls', title:'Aimpuls'},
        {path:'/autozapchasti', component:Autoelektrika, name:'/Автозапчасти', title:'Автозапчасти'},
        {path:'/komplekt', component:Autoelektrika, name:'/Комплект', title:'Комплект'},
        {path:'/novinki', component:Autoelektrika, name:'/Новинки', title:'Новинки'},
        
    ]
})